.btn {
  border: none;
  padding: 8px 15px;
  background-color: rgb(104, 204, 153);
  color: #fff;
  font-weight: 500;
  margin-left: 5px;
  margin-right: 5px;
  font-family: inherit;
  cursor: pointer;
  outline: none;
  box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
  transition: box-shadow 250ms linear;

  border-radius: 10px;
}

.btn:hover,
.btn:focus {
  background-color: rgb(29, 182, 225);
}

.checkbox {
  cursor: pointer;
  width: 24px;
  height: 24px;
}

.text {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 24px;
  margin-left: 24px;
  font-size: 20px;
}
